import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import classnames from "classnames";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import red from "@material-ui/core/colors/red";
import Map from "@material-ui/icons/Map";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import MoreVertIcon from "@material-ui/icons/MoreVert";

const styles = theme => ({
  card: {
    margin: "10px",
    width: 350
  },
  media: {
    height: 0,
    paddingTop: "56.25%" // 16:9
  },
  actions: {
    display: "flex"
  },
  expand: {
    transform: "rotate(0deg)",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest
    }),
    marginLeft: "auto"
  },
  expandOpen: {
    transform: "rotate(180deg)"
  },
  avatar: {
    backgroundColor: red[500]
  }
});

class RecipeReviewCard extends React.Component {
  state = { expanded: false };

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }));
  };
  hanldeSetCenter = () => {
    const {
      latitude,
      longitude,
      setFilter,
      setMapInfo,
      fetchStashPoints
    } = this.props;
    const center = { lat: latitude, lng: longitude };
    const filters = { centre_lat: latitude, centre_lon: longitude };
    Promise.resolve(setMapInfo(center))
      .then(setFilter(filters))
      .then(fetchStashPoints());
  };

  render() {
    const {
      classes,
      name,
      photos,
      address,
      capacity,
      opening_hours = [],
      contact = {}
    } = this.props;

    return (
      <div>
        <Card className={classes.card}>
          <CardHeader
            avatar={
              <Avatar aria-label="Recipe" className={classes.avatar}>
                {name[0]}
              </Avatar>
            }
            action={
              <IconButton>
                <MoreVertIcon />
              </IconButton>
            }
            title={name}
            subheader="September 14, 2016"
          />
          <CardMedia
            className={classes.media}
            image={photos[0]}
            title="Contemplative Reptile"
          />
          <CardContent>
            <Typography component="p">
              <p>{address}</p>
              <p>capacity:{capacity}</p>
              <p>name:{contact.name}</p>
              <p>email:{contact.email}</p>
              <p>phone_number:{contact.phone_number}</p>
            </Typography>
          </CardContent>
          <CardActions className={classes.actions} disableActionSpacing>
            <IconButton aria-label="Map">
              <Map onClick={this.hanldeSetCenter} />
            </IconButton>
            <IconButton
              className={classnames(classes.expand, {
                [classes.expandOpen]: this.state.expanded
              })}
              onClick={this.handleExpandClick}
              aria-expanded={this.state.expanded}
              aria-label="Show more"
            >
              <ExpandMoreIcon />
            </IconButton>
          </CardActions>
          <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
            <CardContent>
              <Typography paragraph variant="body2">
                opening hours:
              </Typography>
              {opening_hours.map((obj, key) => (
                <div
                  key={key}
                  style={{
                    display: "flex",
                    justifyContent: "space-arround",
                    alignItems: "center"
                  }}
                >
                  <div style={{ margin: "5px" }}> Day {obj.day}: </div>
                  <div>
                    {obj.open}-{obj.close}
                  </div>
                </div>
              ))}
            </CardContent>
          </Collapse>
        </Card>
      </div>
    );
  }
}

RecipeReviewCard.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(RecipeReviewCard);



